html, body {
  margin: 0;
  padding: 0;
}

.App-header {
  text-align: center;
  background-color: #282c34;
  min-height: 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
}

.masthead {
	position: absolute;
	right: 15px;
	font-size: 20px;
}

.nav {
	position: absolute;
	left: 0;
}

.App-link {
  color: #61dafb;
}

.content {
  width:90%;
  background-color: white;
  margin: auto;
}

article {
  color:#999;
  font-family: Roboto, futura, sans-serif;
  line-height: 2;
  text-align: justify;
  margin-bottom: 50px;
}

.article-title {
    font-size: 16px;
	font-weight: bold;
    color: #9ab;
}

h4 {
  color:#565;
}


.flex-container {
  display: flex;
  justify-content: space-between;
}
.left-side {
  width: 45%;
  background: #eee;
  height: calc(100vh - 50px);
  padding: 20px;
  overflow-wrap: break-word;
  overflow-y: scroll;
}
.right-side {
  width: 45%;
  background: #ddd;
  padding: 20px;
  overflow-wrap: break-word;
}

.numerator span {
  display: inline-block;
  width: 20px;
  height: 20px;
  line-height:20px;
  text-align: center;
  font-size: .55rem;
  cursor: pointer;
  margin: 2px;
  background-color:gray;
  color: white;
  border-radius: 13px;
}

.non-repeat {
  color: #333;
}
.repeat {
  color: #09f;
}
.repeat-a {
  color: #09f;
}
.repeat-b {
  color: #f90;
}
.to-infinity {
  color: #ccc;
}
.expansion {
  display: block;
  cursor: pointer;
  overflow-wrap: break-word;
  word-break: break-all;
}
.numerators {
}
.show-numerators {
  display:block;
}
.hide-numerators {
  display:none;
}

.square {
  position: absolute;
  width: 20px;
  height: 20px;
  margin: 1px 0 0 1px;
  background-color: #C5CCD2;
}
.a-square {
  background-color: #8AAF9C;
}
.b-square {
  background-color:#66866A;
}
.c-squared {
  position: relative;
  background-color: #dfdfdf;
}
.c-label {
  position: absolute;
  font-size: 13px;
  bottom: -20px;
}
.c-label span {
  vertical-align: super;
}
.b-label {
  position: absolute;
  font-size: 13px;
  bottom: 0px;
  right: -28px;
  color: #66866a;
  -webkit-writing-mode: vertical-rl;
      -ms-writing-mode: tb-rl;
          writing-mode: vertical-rl;
  -webkit-text-orientation: mixed;
          text-orientation: mixed;
}
.b-label span {
  vertical-align: super;
  color: #66866a;
}
.a-label {
  position: absolute;
  font-size: 13px;
  top: -23px;
  left: 0px;
  color: #66866a;
}
.a-label span {
  vertical-align: super;
}

tr {
	cursor: pointer;
}

